/**
 * HeaderComponent
 */
import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { UserService } from '../../../services/user/user.service';


/**
 * 
 * @const pages
 */
const pages = [
    { url: '/ticket', title: '' },
    { url: '/purchase', title: '' },
    { url: '/about', title: '' },
    { url: '/policy', title: '' },
    { url: '/law', title: '' },
    { url: '/privacy', title: '' },
    { url: '/law', title: '' },
    { url: '/benefits', title: ' / ' },
    { url: '/auth/logout', title: '' },
    { url: '/member/mypage', title: 'TOP' },
    { url: '/member/point', title: '' },
    { url: '/member/point/history', title: '' },
    { url: '/member/point/benefits', title: '' },
    { url: '/member/edit', title: '' },
    { url: '/member/edit/credit', title: '' },
    { url: '/member/edit/profile', title: '' },
    { url: '/member/withdraw', title: '' }
];

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
/**
 * 
 * @class HeaderComponent
 * @implements OnInit
 */
export class HeaderComponent implements OnInit {

    public page: {
        url: string;
        title: string;
    };
    public isMenuOpen: boolean;

    constructor(
        public user: UserService,
        private router: Router
    ) { }

    /**
     * 
     * @method ngOnInit
     */
    public ngOnInit() {
        this.isMenuOpen = false;
        this.changePage(this.router.url);
        this.router.events.subscribe((event) => {
            if (event instanceof NavigationEnd) {
                this.changePage(event.url);
            }
        });
    }

    /**
     * 
     * @method menuOpen
     */
    public menuOpen() {
        this.isMenuOpen = true;
    }

    /**
     * 
     * @method menuClose
     */
    public menuClose() {
        this.isMenuOpen = false;
    }

    /**
     * 
     * @method changePage
     */
    private changePage(url: string): void {
        const page = pages.find((value) => {
            return (value.url === url);
        });
        if (page === undefined) {
            this.page = { url: '', title: 'NOT FOUND' };

            return;
        }
        this.page = page;
    }
}
